import React, { useState } from 'react'
import { AsideStyle, Container, Contents } from '../../style/contents.style'
import PageTitle from '../../components/Layout/PageTitle'
import ListVod from '../../components/List/ListVod'
import ToggleOpt from '../../components/List/ToggleOpt';
import SchDataInput from '../../components/Form/SchDataInput';
import SelectBox from '../../components/Form/SchSelectBox';
import SchInput from '../../components/Form/SchInput';


import { SchGroup } from '../../components/Form/form.style';
import Aside from '../../common/Aside/Aside';

const Classroom = () => {
  const selectList1 = [
    { label: "질환군", value: "disease" },
    { label: "피부", value: "skin" },
    { label: "호흡기질환", value: "respiratory" },
  ];
  const selectList2 = [
    { label: "연자명", value: "actor1" },
    { label: "장재원", value: "actor2" },
    { label: "이혜원", value: "actress" },
  ];

  const [showCol, setShowCol] = useState(true);
  const toggleShowCol = (value: boolean) => {
    setShowCol(value);
  };
  

  return (
    <Container id='content'>
      <div className='contents-inner'>
        <Contents>
          <ToggleOpt showCol={showCol} toggleShowCol={toggleShowCol} />
          <PageTitle titleText="임상강좌 VOD"/>
          <SchGroup>
              <SchDataInput />
              <SelectBox selectList={selectList1} />
              <SelectBox selectList={selectList2}  />
              <SchInput />
          </SchGroup>
          <ListVod showCol={showCol} />
        </Contents>
        
        <Aside />
      </div>
    </Container>
  )
}

export default Classroom