import React, { useState } from 'react'
import { Container, Contents, AsideStyle } from '../../style/contents.style'
import PageTitle from '../../components/Layout/PageTitle'
import ToggleOpt from '../../components/List/ToggleOpt'
import ListVod from '../../components/List/ListVod'
import ListLive from '../../components/List/ListLive'
import ListRecomm from '../../components/Main/ListRecomm'
import { BgGray } from './main.style'
import KewordPick from '../../components/Classroom/KewordPick'
import Aside from '../../common/Aside/Aside'

const Main = () => {
  const [showCol, setShowCol] = useState(true);
  const toggleShowCol = () => {
    setShowCol(!showCol);
  };
  
  return (
    <main>
      <BgGray>
        <Container id='content'>
          <div className='contents-inner'>
            <Contents>
              <KewordPick />
              <article>
                <PageTitle titleText="임상강좌 VOD"/>
                <ListVod type="main" showCol={true}/>
              </article>
              <article>
                <PageTitle titleText="Live 세미나"/>
                <ListLive type="main" />
              </article>
            </Contents>
            <Aside />
          </div>
        </Container>
      </BgGray>
      <section>
        <Container pdt="80px">
          <div className='contents-inner'>
            <Contents>
              <article>
                <ToggleOpt showCol={showCol} toggleShowCol={toggleShowCol} />
                <PageTitle titleText="추천 콘텐츠"/>
                <ListRecomm showType={showCol  ? "album" : "list" }  />
              </article>
            </Contents>
          </div>
        </Container>
      </section>
    </main>
  )
}

export default Main