import React from 'react'
import { Link } from 'react-router-dom'
import { Figure, GraySmTXt, ListTit } from './list.style'
import { RoundStyle } from '../Layout/layout.style'
import { Flex } from '../../style/contents.style';
interface Props {
  readonly item: {
    img: string;
    alt: string;
    title: string;
    presenter: string;
    labels: string[];
    point?: string;
  },
  showCol?: boolean;
}
const ListVodItem = ({ item, showCol }: Props) => {
  return (
    <li>
      <Link to="/class/vod" className='hover-underline'>
        <Flex alOpt='flex-start' column={showCol} gap={showCol ? "0" : "20px"}>
          <Figure>
            <img src={item.img} alt={item.alt} />
          </Figure>
          <div>
            {item.labels.map((label, labelIndex) => (
              <RoundStyle
                key={labelIndex}
                $mode={
                  label === "AD" || label === "NEW"
                    ? "gradient"
                    : label === "LIVE"
                    ? "orange"
                    : undefined
                }
                
                className={
                  label === "AD" || label === "NEW" || label === "LIVE"
                    ? "en"
                    : undefined
                }
              >
                {label}
              </RoundStyle>
            ))}
            <ListTit className="title">{item.title} </ListTit>
            <GraySmTXt>{item.presenter}</GraySmTXt>
          </div>
        </Flex>
      </Link>
    </li>
  )
}

export default ListVodItem