import React, { useState } from 'react'
import { Grid } from '../../style/contents.style'
import ListVodItem from './ListVodItem'
import listVodData from '../../data/listVodData'
import DataNone from './DataNone';
import { BtnMore } from '../Layout/layout.style';
export interface ListTypeProps{
  type ?: string;
  showCol ?: boolean
}
const ListVod = ({type, showCol} : ListTypeProps) => {
  let listVodItems = type === "main" ? listVodData.slice(0, 3) : listVodData;
  const [visibleItems, setVisibleItems] = useState(6);
  const listVodLength = listVodItems.length;
  listVodItems = listVodItems.slice(0, visibleItems);
  const handleShowMore = () => {
    setVisibleItems((prev) => prev + 4); 
  };
  return (
    <>
      {showCol && listVodLength>0  && (
         <Grid columns="3">
         {listVodItems.map((item, index) => (
           <ListVodItem key={index} item={item} showCol={showCol}/>
         ))}
       </Grid>
        
      )}
     {!showCol && listVodLength>0 && (
        <Grid columns="1">
          {listVodItems.map((item, index) => (
            <ListVodItem key={index} item={item} />
          ))}
        </Grid>
      )}
      {visibleItems < listVodLength && (
        <BtnMore onClick={handleShowMore}>더보기</BtnMore>
      )}
      {(listVodLength <= 0) &&
      (
        <DataNone />
      )}
    </>
    
  )
}

export default ListVod