import styled from "@emotion/styled"
import { ClampText, MouseOver } from "../../style/variable.style"


export const NewsList = styled.ul`
  li {
    line-height: 1.8;
    a {
      display: block;
      width: 100%;
      font-size:14px;
      ${ClampText.df};
      ${MouseOver.underline};
    }
  }
`

export const BannerSec = styled.div`
 & + div{
   margin-top:10px;
 }
`
export const List = styled.a`
  display: flex;
  gap: 10px;
  .img{
   display:block;
   flex-shrink:0;
   width:86px;
   overflow:hidden;
  }

  .title {
    line-height: 1.2;
    ${ClampText.line2};
    ${MouseOver.underline};
    font-weight:400;
  }
  .txt-small {
    margin-top: 4px;
    font-size:1.2rem;
  }
`;
