import styled from "@emotion/styled";


interface ContainerProps {
  mbb0?: boolean;  
  pdt?: string;  
  $full ?: boolean;  
}
export const Container = styled.div<ContainerProps>`
  width:${props => props.$full ? '100%' : 'var(--cont-width, auto)'};
  margin: 0 auto;
  margin-bottom:${props => (props.mbb0 ? '0' : 'var(--cont-gap, 0)')};
  padding-top:${props => (props.pdt? props.pdt : 'var(--cont-top)')};

  a.hover-underline:hover,
  button.hover-underline:hover{
    .title{
      text-decoration:underline;
    }
  }
  .contents-inner{
    display:flex;
    justify-content:space-between;
  }
`

export const Contents = styled.div`
  flex-basis:var(--lnner-left-width, auto);
  max-width:var(--lnner-left-width, auto);
  position:relative;
`

export const AsideStyle = styled.aside`
  flex-basis:var(--lnner-right-width, auto);
  section+section{
    margin-top:var(--base-gap);
  }
  overflow:hidden;
`
interface FlexProps {
  alOpt?: string; 
  gap?: string;  
  column?: boolean;  
}

export const Flex =styled.div<FlexProps>`
  display:flex;
  align-items: ${(props) => (props.alOpt ? props.alOpt : 'center')};
  gap: ${(props) => (props.gap ? props.gap : '20px')};
  flex-direction:${props => props.column ? 'column' : ''};
  
  .flex-sh0{
    flex-shrink:0;
  }
`
export const FlexSpace = styled(Flex)`
  justify-content:space-between;
  &.mb_column{
    @media (max-width: 1024px) {
      margin-bottom:5rem;
      flex-direction:column;
      align-items:flex-start;
    }
  }
`
interface GridProps {
  columns: string; 
  gap?: string;  
  as?: 'div' | 'ul'; 
}
export const Grid = styled.ul<GridProps>`
  display:grid;
  grid-template-columns: repeat(${(props) => props.columns}, 1fr);
  gap: ${(props) => props.gap || '20px'};
 
`
export const Input = styled.input`
  border:1px solid var(--form-border);
`
interface BtCloseProps {
  position?: number; 
}
export const BtnClose = styled.button<BtCloseProps>`
position:absolute;
top:${(props) => (props.position ? props.position : 0)}px;
right:${(props) => (props.position ? props.position : 0)}px;
z-index:10;
&:before,&:after{
  content:"";
  width:2px;
  height:18px;
  display:block;
  background-color:${(props) => (props.color ? props.color : '#fff')};
}
&:before{
  transform: rotate(45deg);
}
&:after{
  margin-top:-18px;
  transform:rotate(-45deg);
}
`
export const BtnWrap = styled.div`
  text-align:center;
  button+button{
    margin-left:1rem;
  }
`
export const ChkForm = styled.div`
  .check{
    display:none;
  }
  label{
    display:flex;
    font-size:1.6rem;
    word-break:keep-all;
  }
  label:before{
    content:"";
    display:inline-block;  
    border:1px solid var(--form-border);
    width:2.4rem;
    height:2.4rem;
    margin-right:.8rem;
    vertical-align:middle;
    cursor:pointer;
    flex-shrink:0;
    box-sizing:border-box;
  } 
  .check:checked + label:before{
    border:none;
    background:url('') #0c1622 no-repeat center;
    background-size:54%;
  }
`
;