import React from 'react'
import { CaseBox, CaseBoxCont, InfoRow } from '../../pages/community/community.style'
import { Flex } from '../../style/contents.style';
import { Link } from 'react-router-dom';
interface Props {
  value: {
    img: string;
    title: string;
    text: string;
    hospital:string;
    date:string;
    cnt:number;
  }
}
const CaseItem = ({value: {img,title,text,hospital,date,cnt }} : Props) => {
  return (
    <CaseBox>
      <Link to="/community/digCase_boardview" className='hover-underline'>
        <Flex>
          <figure><img src={img} alt=''/></figure>
          <CaseBoxCont>
            <strong className='title'>{title}</strong>
            <p>{text}</p>
            <InfoRow className='info'>
              <span>{hospital}</span>
              <span>{date}</span>
              <span>도움됐어요 <span className='cnt'>{cnt}</span></span>
            </InfoRow>
          </CaseBoxCont>
        </Flex>
      </Link>
    </CaseBox>
  )
}

export default CaseItem