import React from 'react'
import { EventLI } from '../../pages/community/community.style'
interface Props {
  img:string;
  alt ?: string;
  title: string;
  date: string;
  end ?:boolean
}
const EventItem = ({img,alt,title,date,end}:Props) => {
  return (
    <EventLI>
      <a href='#' onClick={(e)=>e.preventDefault()}>
        {end && <div className='cover'>종료되었습니다</div>}
        <figure><img src={img} alt={alt}></img></figure>
        <figcaption>
          <strong>{title}</strong>
          <span>{date}</span>
        </figcaption>
      </a>
    </EventLI>
  )
}

export default EventItem