import React from 'react'
import BoardView from '../../components/Community/BoardView'
import { Container, Contents } from '../../style/contents.style'
import Aside from '../../common/Aside/Aside'

const FreeBoardView = () => {
  return (
    <>
      <Container id="content">
        <div className='contents-inner'>
          <Contents>
            <BoardView 
              value={{
                title:"이미지 사이트 추천해주세요",
                writer:"김가영",
                date:"2022-10-12",
                cnt:5,
              }}
            />
          </Contents>
          <Aside />
        </div>
      </Container>
    </>
  )
}

export default FreeBoardView