import React from 'react'
import { Grid } from '../../style/contents.style'
import ListLiveItem from './ListLiveItem';
import listLiveData from '../../data/listLive'
export interface ListTypeProps{
  type ?: string;
}
const ListLive = ({type} : ListTypeProps ) => {
  const listLiveItems = type === "main" ? listLiveData.slice(0, 3) : listLiveData;
  return (
    <Grid columns="3">
      {listLiveItems.map((item, index) => (
        <ListLiveItem key={index} item={item} />
      ))}
    </Grid>
  )
}

export default ListLive