import React from 'react'
import { Container, Contents } from '../../style/contents.style'
import { SchGroup } from '../../components/Form/form.style';
import SchInput from '../../components/Form/SchInput'
import SchDataInput from '../../components/Form/SchDataInput'
import Visual from '../../components/Layout/Visual'
import { CommunityTb } from '../community/community.style';
import { BtnMore } from '../../components/Layout/layout.style';
import Aside from '../../common/Aside/Aside';
import { Link } from 'react-router-dom';

const VetNews = () => {
  return (
    <>
      <Container $full pdt="70px" id="content">
        <Visual
          bgType="news" 
          title="베터빌 뉴스" 
          subTitle="수의학계 각종 현안 및 관심사와 연관된 최신 뉴스를 제공합니다."
        />
      </Container>
      <Container pdt="0">
        <div className='contents-inner'>
          <Contents>
            <SchGroup schWidth="620px">
              <SchDataInput />
              <SchInput />
            </SchGroup>
            <CommunityTb className='news'>
              <table>
                <caption>번호,제목, 작성자, 작성일, 추천으로 구성된 테이블</caption>
                <colgroup>
                  <col style={{ width: '120px' }} />
                  <col style={{ width: 'auto' }} />
                </colgroup>
                <tbody>
                  <tr>
                    <td>2022.05.06</td>
                    <td className="title"><Link to="/library/newsview">'길고양이 TNR, 마당개 중성화 문제 많다' 수의사회 보이콧 움직임</Link></td>
                  </tr>
                  <tr>
                    <td>2022.05.06</td>
                    <td className="title"><Link to="/library/newsview">NSIAD 사용에 따른 Ulcer 예방을 위한 처방증례 Ulcer 예방을 위한 처방증례 Ulcer 예방(정제, 캡슐제, 과립자) NSIAD 사용에 따른 Ulcer 예방을 위한 처방증례 Ulcer 예방을 위한 처방증례 Ulcer 예방(정제, 캡슐제, 과립자) NSIAD 사용에 따른 Ulcer 예방을 위한 처방증례</Link></td>
                  </tr>
                  <tr>
                    <td>2022.05.06</td>
                    <td className="title"><Link to="/library/newsview">'길고양이 TNR, 마당개 중성화 문제 많다' 수의사회 보이콧 움직임</Link></td>
                  </tr>
                </tbody>
              </table>
              <BtnMore>더보기</BtnMore>
            </CommunityTb>
          </Contents>
          <Aside />
        </div>
      </Container>
    </>
  )
}

export default VetNews