import styled from "@emotion/styled"
import { ClampText, IcoCollect, PosCenterY } from "../../style/variable.style";
import { css } from "@emotion/react";

export const LiveGuideGrp = styled.div`
  & > span {
    position: relative;
    margin-right: 16px;
    padding-left: 14px;
    font-size:1.4rem;
    & + span:before {
      content: "";
      ${PosCenterY}
      left:0;
      width: 1px;
      height: 14px;
      background: var(--form-border);
    }
    span {
      margin-top: -2px;
    }
  }
`
interface LabelProp {
  $purple?:boolean;
}
export const IcoLabel = styled.span<LabelProp>`
  display: inline-block;
  width: 18px;
  height: 18px;
  line-height: 16px;
  background-color: #ff6879;
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  vertical-align: middle;
  font-style: normal;
  font-size:1.2rem;
  background:${props => props.$purple && ' #866ae2'};
 
`
export const BtnLine = styled.button`
  padding: 0 10px;
  line-height: 34px;
  font-size:1.6rem;
  color: #111;
  border: 1px solid #111;
`
export const Video = styled.div`
  width:100%;
  height:487px;
  overflow:hidden;
  margin-bottom:var(--base-gap);
  iframe{ 
    width:100%;
    height:100%;
  }
`
export const VodTitle = styled.div`
  .cate{
    --h:26px;
    padding:0px 10px;
    line-height:24px;
    font-size:1.6rem;
    font-weight:600;
  }
  h3{
    margin:8px 0;
    ${ClampText.line2};
    font-size:2.4rem;
    font-weight:500;
  }
  .hash span{
    display:inline-block;
    margin-right:4px;
    font-size:1.4rem;
    font-weight:400;
    color:var(--c-gray);
  }
`

export const LabelGrp = styled.div`
  margin:12px 0;
`
interface LabelProps {
  $mode?: "purple" | "skyblue";
}
export const Label = styled.span<LabelProps>`
    display:inline-block;
    margin-right:4px;
    padding:0 8px;
    line-height:24px;
    font-size:1.2rem;   
    ${(props) => {
    switch (props.$mode) {
      case "purple":
        return css`
          color:#866AE2;
          border:1px solid #866AE2;
      `;
      case "skyblue":
        return css`
          color:var(--primary-color);
          border:1px solid var(--primary-color);
      `;
      default:
        return css`
          color:var(--c-gray);
          border:1px solid var(--c-gray);
      `;
    }
  } 
}
`
export const VodCont = styled.ul`
  em{
    color:var(--c-gray);
    margin-right:12px;
    font-size:1.4rem;
  }
  span{
    font-size:1.4rem;
    a{
      text-decoration:underline;
    }
  }
`
export const ProList = styled.ul`
  position:relative;
  margin-top:var(--base-gap);
  border-top:1px solid var(--form-border);
  & >li{
    position:relative;
    height:122px;
    padding:20px 0;
    border-bottom:1px solid var(--form-border);
    overflow:hidden;
    figure{
      flex-shrink:0;
      width:121px;
      height:117px;
      overflow:hidden;
    }
    &.open{
      height:auto;
    }
  }
`

export const ListInfo = styled.div`
  strong{
    display:block;
    margin-bottom:4px;
    font-size:2rem;
    font-weight:400;
  }
  .belong{
    display:block;
    margin-bottom:18px;
    font-size:1.4rem;
    color:var(--c-gray);
  }
  .info-list{
    color:var(--c-gray);
    &>li{
      font-size:1.4rem;
      span{
        margin-left:10px;
        color:var(--primary-color);
      }
      ul{
        margin-top:10px;
      }
    }
  }
`
export const BtnToggle = styled.button`
   position:absolute;
   bottom:20px;
   right:20px;
   &:after{
    content:"";
    display:inline-block;
    width:8px;
    height:8px;
    margin-top:-8px;
    margin-left:6px;
    vertical-align:middle;
    border-left:1px solid #000;
    border-bottom:1px solid #000;
    transform:rotate(-45deg);
   }
  &.open:after{
   margin-top:2px;
   transform:rotate(135deg);
  }
`

export const ProIntroBox = styled.div`
  margin:40px 0px 20px;
  border:1px solid var(--form-border);
  padding:40px 20px;
  .program-list{
    padding:30px;
    background:#F2F2F2;
    font-size:1.4rem;
    .time{
      margin-right:20px;
      letter-spacing:-0.06rem;
      color:var(--primary-color);
      text-decoration:underline;
    }
  }
`
export const FavRow = styled.div`
  text-align:center;
  .num{
    font-size:1.4rem;
  }
`
export const Guidebox = styled.div`
  margin:20px 0px 40px;
  padding:20px;
  border:1px solid var(--form-border);
  font-size:1.4rem;
  strong{
    display:block;
    position:relative;
    margin-bottom:12px;
    padding-left:28px;
    font-size:1.6rem;
    &:before{
      content:"";
      position:absolute;
      top:50%;
      left:0;
      width:18px;
      height:18px;
      transform:translateY(-50%);
      ${IcoCollect};
      background-position:-32px -117px;
      background-repeat:no-repeat;
    }
  }

`
export const LiveTable = styled.div`
  margin-top:20px;
  border-top:1px solid #000;
  table{
    th,td{
      padding:28px 4px;
      font-size:1.6rem;
      vertical-align:top;
      text-align:center;  
      line-height:1.2;
      strong{
        display:block;
      }
    }
    th{
      span{
        font-weight:400;
      }
    }
    td.time{
      color:#6B93D4;
      background:#ECF0F9;
    }
    td.detail{
      padding:28px 16px;
      text-align:left;
      border-bottom:1px solid var(--form-border);
    }
    tr:nth-of-type(even) .time{
      color:#45C1A2;
      background:#EAF8F5;
    }
  }
  
`
export const Detail = styled.div`
  em{
    display:block;
    margin-bottom:10px;
  }
  .title{
    display:flex;
    align-items:center;
    gap:10px;
    span{
      flex-shrink:0;
    }
    strong{
      width:500px;
      ${ClampText.df}
      font-size:1.8rem;
    }
  }
`
export const Req = styled.div`
  .rd-label{
    --h:26px;
    padding:0px 16px;
    font-size:1.4rem;
    font-weight:500;
  }
  .num{
    margin-top:6px;
    color:#000;
    .limit{
      color:var(--c-light-gray);
    }
  }
`