import React from 'react'
import { useLocation } from 'react-router-dom';
import BanRight from '../../images/banner_right.jpg';
import BanTopic from '../../images/banner_topic.jpg';
import BannerItem from '../../components/Aside/BannerItem'
import News from '../../components/Aside/News';
import ImgAside from '../../images/aside_img.jpg';
import AsideList from '../../components/Aside/AsideList';
import { AsideStyle } from '../../style/contents.style';
import PageTitle from '../../components/Layout/PageTitle';

const Aside = () => {
  const location = useLocation(); // 현재 경로 가져오기

  const renderAsideContent = () => {
    switch (location.pathname) {
      case '/':
        return ( 
        <>
          <section>
            <PageTitle titleText="베터빌 인기 뉴스" type='aside'/>
            <News />
          </section>
          <section>
            <BannerItem img={BanRight} />
            <BannerItem img={BanTopic} />
          </section>
        </>
      )
      case '/class':
        return (
          <>
            <section>
              <PageTitle titleText="추천 VOD" type='aside'/>
              <AsideList imgAside={ImgAside} presenter="이정은 교수 (ㅇㅇㅇ병원 00내과)"/>
            </section>
            <section>
              <BannerItem img={BanRight} />
            </section>
          </>
        );
        case '/class/vod':
        return (
          <>
            <section>
              <PageTitle titleText="추천 VOD" type='aside'/>
              <AsideList imgAside={ImgAside} presenter="이정은 교수 (ㅇㅇㅇ병원 00내과)"/>
            </section>
            <section>
              <BannerItem img={BanRight} />
            </section>
          </>
        );
        case '/class/live':
          return (
            <>
              <section>
                <PageTitle titleText="추천 VOD" type='aside'/>
                <AsideList imgAside={ImgAside} presenter="이정은 교수 (ㅇㅇㅇ병원 00내과)"/>
              </section>
              <section>
                <BannerItem img={BanRight} />
              </section>
            </>
          );
        case '/library':
          return (
            <>
              <section>
                <BannerItem img={BanRight} />
              </section>
            </>
        );
        case '/library/newsview':
          return (
            <>
              <section>
                <PageTitle titleText="베터빌 인기 뉴스" type='aside'/>
                <News />
              </section>
              <section>
                <BannerItem img={BanRight} />
              </section>
            </>
        );
        case '/community':
          return (
            <>
              <section>
                <BannerItem img={BanTopic} />
              </section>
            </>
        );
        case '/community/dignos_case':
          return (
            <>
              <section>
                <PageTitle titleText="추천 VOD" type='aside'/>
                <AsideList imgAside={ImgAside} presenter="이정은 교수 (ㅇㅇㅇ병원 00내과)"/>
              </section>
              <section>
                <BannerItem img={BanTopic} />
              </section>
            </>
        );
      default:
        return null; // 다른 경로에서는 Aside를 비우거나 다른 컴포넌트 렌더링
    }
  };

  return <AsideStyle>{renderAsideContent()}</AsideStyle>;
}

export default Aside