import styled from "@emotion/styled"
import { ClampText, IcoCollect, IcoSch } from "../../style/variable.style"


export const Figure = styled.figure`
  height:158px;
  position:relative;
  margin-bottom:16px;
  
  .label{
    position:absolute;
    left:6px;
    bottom:10px;
  }
   
`

export const ListTit = styled.strong`
  display:block;
  margin-top:10px;
  line-height:var(--txt-lh);
  font-size:1.6rem;
  font-weight:400;
  ${ClampText.line2}
`
export const GraySmTXt =styled.span`
   display:block;
   margin-top: 6px;
   font-size:1.4rem;
   color: var(--c-gray);

`
export const Cate = styled.span`
  display:block;
  font-size:1.6rem;
  font-weight:700;
`

export const ListDesc = styled.p`
  margin-top:6px;
  font-size:1.6rem;
  color:var(--c-gray);
  ${ClampText.line2}
`

export const ToggleChoice = styled.div`
  display:flex;
  justify-content:flex-end;
  position:absolute;
  top:30px;
  right:0;
  button {
    display:block;
    width: 1.6rem;
    height: 1.6rem;
    ${IcoCollect};
    background-position: 0px -30px;
    &.album {
      &.on {
        background-position-x: -22px;
      }
    }
    &.list {
      background-position-x: -44px;
      &.on {
        background-position-x: -67px;
      }
    }
    &:last-of-type {
      margin-left: 10px;
    }
  }
`

export const DataNoneStyle = styled.div`
  display: flex;
  height: 26vh;
  align-items: center;
  justify-content: center;
  font-size:2.4rem;
  .ico {
    ${IcoSch};
    margin-right: 4px;
  }
`;