import React, { useState } from 'react'
import { Container, Flex, FlexSpace } from '../../style/contents.style'
import {Gnb, HeaderFix, Identify, Member, NaviWrap, NavList, SkipNav, Submenu } from './com_layout.style'
import { Link, useLocation } from 'react-router-dom'
import SearchBox from './SearchBox'
import AllMenue from './AllMenue'
import VetLogo from './VetLogo'
import { HashLink } from 'react-router-hash-link';
const Header = () => {
  const [isActive, setIsActive] = useState(false);
  const location = useLocation();
  const handleButtonClick = () => {
    setIsActive(!isActive);
  };
  return (
    <HeaderFix>
       <SkipNav>
          <HashLink smooth to="#content">
            본문 바로가기
          </HashLink>
      </SkipNav>
      <Container mbb0 pdt="0">
        <FlexSpace>
          <NaviWrap>
            <VetLogo />
            <nav>
              <NavList>
                <li>
                  <Link to="/class" 
                    className={`link ${location.pathname.startsWith("/class") ? "on" : ""}`}
                  >
                    클래스룸</Link>
                  <Submenu>
                    <li>
                      <Link to="/class">임상강좌 VOD</Link>
                    </li>
                    <li>
                      <Link to="/class/live">Live 세미나</Link>
                    </li>
                  </Submenu>
                </li>
                <li>
                <Link to="/library" 
                  className={`link ${location.pathname.startsWith("/library") ? "on" : ""}`}
                  >
                  라이브러리</Link>
                  <Submenu>
                    <li>
                      <Link to="/library">베터빌 뉴스</Link>
                    </li>
                    <li>
                      <Link to="/library/reportsch">논문 검색</Link>
                    </li>
                  </Submenu>
                </li>
                <li>
                  <Link to="/community" 
                    className={`link ${location.pathname.startsWith("/community") ? "on" : ""}`}
                  >
                    커뮤니티</Link>
                  <Submenu>
                    <li>
                      <Link to="/community">자유 게시판</Link>
                      {/* <Link to="/community/tip">실전 진료 Tip</Link> */}
                    </li>
                    <li>
                      <Link to="/community/dignos_case">진단케이스</Link>
                    </li>
                    <li>
                      <Link to="/community/event">이벤트</Link>
                    </li>
                  </Submenu>
                </li>
              </NavList>
            </nav>
          </NaviWrap>
          <Flex>
            <Identify>김베터 닥터</Identify>
            <Member>
              <button aria-label='멤버'><span className="blind">멤버</span> <span className='ico-alram'>알람</span></button>
              <Submenu>
                <li><a href="#">마이페이지</a></li>
                <li><a href="#">고객센터</a></li>
                <li><a href="#">1:1문의하기</a></li>
                <li><a href="#">공지사항</a></li>
                <li><a href="#">로그아웃</a></li>
              </Submenu>
            </Member>
            <SearchBox />
            <Gnb onClick={handleButtonClick} className={isActive ? "on" : ""}>
              <span className="ico-menu">전체메뉴</span>
            </Gnb>
          </Flex>
        </FlexSpace>
        <AllMenue isActive= {isActive} />

      </Container>
    </HeaderFix>
   
  )
}

export default Header