import React from 'react'
import { Container, Contents } from '../../style/contents.style'
import Visual from '../../components/Layout/Visual'
import SchInput from '../../components/Form/SchInput'
import SelectBox from '../../components/Form/SchSelectBox'
import { SchGroup } from '../../components/Form/form.style'
import { BtnMore } from '../../components/Layout/layout.style'
import { CommunityTb } from './community.style'
import Aside from '../../common/Aside/Aside'
import { Link } from 'react-router-dom'
const select_list = [
  { label: '제목', value: 'title' },
  { label: '제목+내용', value: 'total' },
  { label: '내용', value: 'content' }
];
const FreeBoard = () => {
  return (
    <>
      <Container $full pdt="70px" id="content">
        <Visual 
          bgType="freeBoard" 
          title="자유게시판" 
          subTitle="회원들과 자유롭게 이야기를 나누고, 함께 만들어가는 공간입니다."
        />
      </Container>
      <Container pdt="0">
        <div className='contents-inner'>
          <Contents>  
            <SchGroup 
              schWidth="400px" 
              mbbVal="var(--base-gap)"
              bdColor="var(--form-border)"
            >
              <SelectBox selectList={select_list}/>
              <SchInput />
            </SchGroup>
            <CommunityTb>
              <table>
                <caption>번호,제목, 작성자, 작성일, 추천으로 구성된 테이블</caption>
                <colgroup>
                  <col style={{ width: '80px' }} />
                  <col style={{ width: 'auto' }} />
                  <col style={{ width: '185px' }} />
                  <col style={{ width: '100px' }} />
                  <col style={{ width: '80px' }} />
                </colgroup>
                <thead><tr><th scope="col">번호</th><th scope="col" className="align_left">제목</th><th scope="col">작성자</th><th scope="col">작성일</th><th scope="col">추천</th></tr></thead>
                <tbody><tr><td>215</td><td className="title"><Link to="/community/free_boardview">이미지 사이트 추천해주세요</Link><span className="comm-cnt">[13]</span><i className="ico_new"></i></td><td>홍길동</td><td>2022-10-04</td><td>406</td></tr><tr><td>214</td><td className="title"><Link to="/community/free_boardview">세미나 수강 및 설문조사</Link><span className="comm-cnt">[2]</span></td><td>김사무엘라</td><td>2023-10-04</td><td>406</td></tr><tr><td>213</td><td className="title"><Link to="/community/free_boardview">이미지 사이트 추천해주세요</Link></td><td>동물사랑협회</td><td>2022-10-04</td><td>406</td></tr></tbody>
              </table>
              <BtnMore>더보기</BtnMore>
            </CommunityTb>
          </Contents>
          <Aside />
        </div>
      </Container>
    </>
  )
}

export default FreeBoard