import React from 'react'
import { LogoBox, ReportLi } from '../../pages/community/community.style'
interface Props {
  titleText: string;
  desc: string;
  bgClass ?:String
}
const ReportItem = ({titleText, desc, bgClass} : Props ) => {
  return (
    <ReportLi>
      <a href='#' onClick={(e)=> e.preventDefault()}>
        <div className='info'>
          <h3>{titleText}</h3>
          <p>{desc}</p>
        </div>
        <LogoBox>
          <span className={`journal ${bgClass}`}></span>
        </LogoBox>
      </a>
    </ReportLi>
  )
}

export default ReportItem