import React from 'react'
import { Link } from 'react-router-dom'
import { Figure, GraySmTXt, ListTit } from './list.style'
import { RoundStyle } from '../Layout/layout.style';
interface Props {
  readonly item: {
    img: string;
    alt: string;
    title: string;
    date: string;
  };
}
const ListLiveItem = ({ item }: Props) => {
  return (
    <li>
      <Link to="/class/vod" className='hover-underline'>
        <Figure>
          <img src={item.img} alt={item.alt} />
          <RoundStyle className='label' $mode="boldGreen">호흡기질환</RoundStyle>
        </Figure>
        <div>
          <ListTit className="title">{item.title} </ListTit>
          <GraySmTXt>{item.date}</GraySmTXt>
        </div>
      </Link>
    </li>
  )
}

export default ListLiveItem