import { ToggleChoice } from './list.style'
export interface ToggleProps {
  showCol: boolean;
  toggleShowCol?: (value: boolean) => void; 
}

const ToggleOpt = ({ showCol, toggleShowCol }: ToggleProps) => {
  const setShowCol = (value: boolean) => {
    if (showCol !== value && toggleShowCol) {
      toggleShowCol(value); // 상태가 다를 때만 부모의 toggleShowCol 실행
    }
  };
  return (
    <ToggleChoice>
      <button
        className={`album ${showCol ? " on" : ""}`}
        onClick={() => setShowCol(true)}
      ><span className='blind'>앨범형</span></button>
      <button
        className={`list ${!showCol ? "on" : ""}`}
        onClick={() => setShowCol(false)}
      ><span className='blind'>리스트형</span></button>
    </ToggleChoice>
  )
}


export default ToggleOpt