import React from 'react'
import { List } from './aside.style'
interface Props {
  readonly imgAside : string;
  presenter?: string;
}
const AsideList = ({imgAside, presenter} : Props) => {
  return (
    <List href="#">
      <span className="img">
        <img src={imgAside} alt="" />
      </span>
      <div className="cont">
        <strong className="title">
          Clinical Implication of Glycemic Variability Glycemic Variabilit
        </strong>
        {presenter && <p className="txt-small">{presenter}</p>}
      </div>
    </List>
  )
}

export default AsideList