import React from 'react'
import { TitleAsid, TitleH2 } from './layout.style';

interface TitleProps {
  titleText: string;
  type ?: string;
}
const PageTitle = ({ titleText,type } : TitleProps) => {
  return (
    <>
      {type ==="aside" ?
        <TitleAsid>{titleText}</TitleAsid>
        : <TitleH2>{titleText}</TitleH2>
      }
    </>
  )
}

export default PageTitle