import { css } from '@emotion/react';
import IcoCommon from '../images/ico_common.png';

export const IcoCollect = css`
  background-image: url(${IcoCommon});
  background-repeat: no-repeat;
`;

export const FlexDefault = css`
  display:flex;
  align-items:center;
`
export const ClampText = {
  df: css`
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  `,
  line2: css`
    display:-webkit-box;
    overflow:hidden;
    text-overflow:ellipsis;
    line-clamp: 2;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
     word-break:keep-all;
  `

}
export const IcoSch = css`
  display: inline-block;
  width: 24px;
  height: 30px;
  background-image: url(${IcoCommon});
  background-position: -33px 7px;
`;

export const PosCenterY = css`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
`
/* 마우스 over */
export const MouseOver = {
  color: css`
    &:hover {
      color: var(--primary-color);
    }
  `,
  btnHover: css`
    &:hover {
      opacity: 0.9;
      box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
    }
  `,
  underline:css`
    &:hover {
      text-decoration:underline;
    }
  `
};

/*scroll style */
export const ScrollStyle = css`
  padding: 12px 15px;
  overflow-y: auto;
  border: 1px solid #e6e6e6;
  box-sizing: border-box;
  &::-webkit-scrollbar {
    width: 4px;
    background-color: #eaeaea;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #d6d6d6;
  }
`