import React from 'react'
import { VisualWrap } from './layout.style'
import { Container } from '../../style/contents.style'

interface Props {
  bgType?: "news" | "freeBoard" | "report" | "tip" | "case"; 
  readonly title: string;
  readonly subTitle: string;
}
const Visual = ({bgType, title, subTitle} : Props) => {
  return (
    <VisualWrap $bg={bgType}>
      <Container pdt="0">
        <div className="visual_inner">
          <h2>{title}</h2>
          <p>{subTitle}</p>
        </div>
      </Container>
    </VisualWrap>
  )
}

export default Visual