import styled from "@emotion/styled"
import { css } from "@emotion/react";
import VisualNews from '../../images/visual_news.jpg';
import VisualBoard from '../../images/visual_board.jpg';
import VisualReport from '../../images/visual_report.jpg';
import VisualTip from '../../images/visual_tip.jpg';
import VisualCase from '../../images/visual_case.jpg';
import { IcoCollect } from "../../style/variable.style";


export const TitleH2 = styled.h2`
  font-size:2.2rem;
  margin:40px 0 20px;
`
export const TitleAsid = styled.h2`
 margin-bottom:20px;
 font-size:1.8rem;
`
interface LabelProps {
  $mode?: "gradient" | "orange" | "boldGreen" | "gray" | "lineGray";
}
export const RoundStyle = styled.span<LabelProps>`
  display:inline-block;
  --h:22px;
  font-size:1.2rem;
  height:var(--h,22px);
  line-height:var(--h,22px);
  vertical-align:middle;
  padding:0 8px;
  border-radius:var(--bd-radius);
  color:#fff;
  box-sizing:border-box;
  transition: var(--trans-3s);
  & + span{
    margin-left:4px;
  }
  &.en {
    line-height: 20px;
  }
  ${(props) => {
    switch (props.$mode) {
      case "gradient":
        return css`
         background:var(--c-gradient);
        `;
      case "orange":
        return css`
          background:var(--c-orange);
        `;
      case "boldGreen":
        return css`
          background:var(--c-boldgreen);
        `;
      case "gray":
        return css`
          background:#e6e6e6;
          color:var(--c-gray);
          &:hover {
            background: #d4d4d4;
          }
        `;
      case "lineGray":
        return css`
          color:var(--c-light-gray);
          border:1px solid var(--c-light-gray);
        `
      default:
        return css`
          color:var(--primary-color);
          border:1px solid var(--primary-color);
      `;
    }
  }}
`
export const BtnMore = styled.button`
  display: block;
  margin: 40px auto 0px;
  padding: 0 32px 0 16px;
  min-width: 112px;
  line-height: 30px;
  border: 1px solid var(--form-border);
  color: var(--c-gray);
  border-radius:30px;
  font-size: 1.4rem;
  font-weight: 500;
  position: relative;
  &:after {
    content: "";
    position: absolute;
    top: 34%;
    right: 10px;
    vertical-align: 3px;
    width: 5px;
    height: 5px;
    margin-left: 10px;
    border-left: 2px solid var(--c-gray);
    border-bottom: 2px solid var(--c-gray);
    transform: rotate(-45deg);
  }
`

interface VisualProps {
  $bg ?: "news" | "freeBoard" | "report" | "tip" | "case" ;
}
export const VisualWrap = styled.div<VisualProps>`
  height:294px;
  padding-top:120px;
  box-sizing: border-box;
  color:#fff;

  ${(props) => {
    if (props.$bg === "news") {
      return css`
        background: url(${VisualNews});
      `;
    }else if (props.$bg === "freeBoard") {
      return css`
        background: url(${VisualBoard});
      `;
    }
    else if (props.$bg === "report") {
      return css`
        background: url(${VisualReport});
      `;
    }
    else if (props.$bg === "tip") {
      return css`
        background: url(${VisualTip});
      `;
    }else {
      return css`
        background: url(${VisualCase});
      `;
    }
  }}

  .visual_inner{
    h2{
        margin-bottom:16px;
        padding-bottom:25px;
        border-bottom:1px solid #fff;
        font-size:3rem;
        font-weight:600;
    }
    p{
        font-size:1.6rem;
    }
}
`
export const HeartIco = styled.button`
display: block;
margin: 0 auto;
width: 22px;
height: 20px;
${IcoCollect};
background-position: -214px -94px;
&.on {
  background-position-x: -177px;
}
`;
