import styled from "@emotion/styled";
import IcoLogo from '../../images/logo.png';
import { css } from "@emotion/react";
import { FlexDefault, IcoCollect, MouseOver, PosCenterY } from "../../style/variable.style";

const HeaderH = css`
  height:70px;
`;

export const HeaderFix = styled.header`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  ${HeaderH}
  z-index: 100;
  background-color: #fff;
  box-sizing: border-box;
  border-bottom: 1px solid #ccc;
  .header_inner {
    height: 70px;
    > div {
    }
  }
`
export const SkipNav = styled.div`
    position:relative;
    z-index:9999;
    a{
        position: absolute; 
        top: -200px; 
        left:0; 
        width: 100%; 
        line-height: 30px; 
        color:#fff; 
        background-color: #00befc;
        text-align: center; 
        font-size:1.6rem;
    }
    a:focus, a:active{top: 0;}
}
`;
export const Logo = styled.span`
  display:block;
  width:180px;
  height:38px;
  background:url(${IcoLogo}) no-repeat;
`

export const NaviWrap = styled.div`
 ${FlexDefault};
 gap:40px;
 ${HeaderH};
`
export const NavList = styled.ul`
  ${FlexDefault};
  gap:30px;
  >li{
    position:relative;
  }
  >li:hover {
    ul {
      display: block;
    }
  }
  .link{
    display:block;
    padding:22px 0;
    font-size:1.8rem;
    font-weight:700;
    ${MouseOver.color}
    &.on{
      color:var(--primary-color);
    }
  }
  
`
export const Submenu = styled.ul`
  display: none;
  position: absolute;
  top: 70px;
  left: -16px;
  min-width: 127px;
  padding: 16px;
  border: 1px solid var(--form-border);
  transition:.2s;
  z-index:90;
  background: #fff;
  box-sizing: border-box;
  li {
    line-height: 1.8;
  }
  a {
    display: block;
    font-size:1.4rem;
    font-weight:normal;
    ${MouseOver.color}
    text-align:left;
  }
  &.on {
    opacity: 1;
    visibility: visible;
  }
`
export const Member = styled.div`
  position: relative;
   ${HeaderH}
   ${FlexDefault}
  button{
    display: inline-block;
    position: relative;
    width: 28px;
    &:before {
      content: "";
      display: block;
      width: 28px;
      height: 24px;
      ${IcoCollect};
      background-position: 0 0;
    }
    .ico-alram {
      position: absolute;
      width: 7px;
      height: 7px;
      top: -3px;
      right: 0;
      background: #4aff88;
      border-radius: 50%;
      text-indent: -9999em;
    }
  }

  &:hover ul {
    display: block;
  }
`


export const Identify = styled.span`
  font-size:1.4rem;
`
export const Search = styled.div`
  border: 2px solid rgb(0, 190, 252);
  width: 260px;
  height: 40px;
  padding-right:40px;
  box-sizing: border-box;
  position: relative;
  input[type="search"] {
    width: 100%;
    line-height: 34px;
    padding-left:10px;
    border:none;
    box-sizing:border-box;
  }
  .btn-sch {
    ${PosCenterY};
    width: 40px;
    height: 100%;
    ${IcoCollect};
    background-position: -24px 7px;
  }
`
export const Gnb = styled.button`
  position: relative;
  width: 24px;
  height: 14px;
  &:before,
  &:after {
    content: "";
  }
  &:before,
  &:after,
  .ico-menu {
    position: absolute;
    left: 0;
    height: 2px;
    background: #000;
    transition:.2s;
  }
  &:before {
    top: 0;
    width: 100%;
  }
  .ico-menu {
    text-indent: -9999em;
    top: 6px;
    width: 18px;
  }
  &:after {
    top: 12px;
    width: 12px;
  }

  &.on {
    .ico-menu {
      display: none;
    }
    &:before {
      transform: translateY(7px) rotate(45deg);
    }
    &:after {
      width: 24px;
      transform: translateY(-5px) rotate(-45deg);
    }
  }
`

export const AllMenuStyle = styled.menu`
  display: none;
  position: absolute;
  top: 70px;
  left: 0;
  right: 0;
  width: 100%;
  padding: 80px 0;
  background-color: #fff;
  border-bottom: 1px solid var(--form-border);
  z-index: 92;
  h2 {
    font-size:2.4rem;
    font-weight:700;
    margin-bottom: 20px;
  }
  .col {
    width: 24%;
    padding: 0 10px;
    box-sizing: border-box;
    li {
      line-height: 2.2;
      a {
        ${MouseOver.color};
        font-size:1.6rem;
      }
    }
  }
  &.on {
    display: block;
  }
`

export const FooterLayout = styled.footer`
  border-top: 1px solid var(--form-border); 
  
  .copy {
   margin-top: 20px;
   font-size:1.3rem;
   color: var(--c-light-gray);
  }
`

export const SiteLink = styled.div`
  margin-top: 14px;
  margin-bottom: 10px;
  a {
    display: inline-block;
    position: relative;
    padding: 0 6px;
    font-size:1.4rem;
    font-weight:normal;
    color: #5d5d5d;
    &:first-of-type {
      padding-left: 0;
    }
    & + a:before {
      content: "";
      position: absolute;
      ${PosCenterY}
      left:0;
      width: 1px;
      height: 10px;
      background-color: var(--c-gray);
    }
    &.point{
      color:var(--primary-color);
    }
  }
`
export const Address = styled.address`
  color: var(--c-light-gray);
  font-style:normal;
  font-size:1.4rem;
  & > span {
    margin-right: 16px;
  }
`
export const FamliySite = styled.div`
  position: relative;
  width: 228px;
  button.site_box {
    position: relative;
    width: 100%;
    line-height: 33px;
    padding: 0 14px;
    background-color: #f2f2f2;
    text-align: left;
    font-size:1.4rem;
    box-sizing:border-box;
    &:after {
      content: "";
      ${PosCenterY}
      right:12px;
      width: 12px;
      height: 12px;
      ${IcoCollect}
      background-position: -200px -4px;
    }
  }
  .sites {
    display: none;
    position: absolute;
    bottom: 33px;
    left: 0;
    width: 100%;
    border: 1px solid var(--form-border);
    background-color: #fff;
    padding: 10px 0;
    box-sizing: border-box;
    a {
      display: block;
      padding: 6px 10px;
      ${MouseOver.color};
    }
    &.open {
      display: block;
    }
  }
`;