import React from 'react'
import { Container, Contents } from '../../style/contents.style'
import Visual from '../../components/Layout/Visual'
import { SchGroup } from '../../components/Form/form.style'
import SchInput from '../../components/Form/SchInput'
import { ListTop } from './community.style'
import CaseItem from '../../components/Community/CaseItem'
import dogImg from '../../images/dog_sample.jpg';
import Aside from '../../common/Aside/Aside'
const DignoseCase = () => {
  return (
    <>
      <Container $full pdt="70px" id="content">
        <Visual
          bgType="case" 
          title="진단 케이스" 
          subTitle="임상에 도움이 되는 다양한 진단케이스와 처방 노하우, 처방 가이드를 제공합니다."
        />
      </Container>
      <Container pdt="0">
        <div className='contents-inner'>
          <Contents>
            <SchGroup schWidth="360px" mbbVal="40px">
              <SchInput />
            </SchGroup>
            <ListTop>
              <strong>전체(10)</strong>
              <ul>
                <li><a href='#' onClick={(e)=> e.preventDefault()} className='on'>최신순</a></li>
                <li><a href='#' onClick={(e)=> e.preventDefault()}>조회순</a></li>
              </ul>
            </ListTop>
            <div>
              <CaseItem 
                value={{
                  img : dogImg,
                  title : "반려동물 시술에 대한 교육자료 공유 반려동물 시술에 대한 교육자료 공유 반려동물 시술에 대한 교육자료 공유",
                  text : "미국 항공우주국 홈페이지에 들어가면 누구나 허블 우주 망원경으로 관측해 촬영한 장엄한 우주 사진을 볼 수 있다. 장엄한 우주 사진을 볼 수 있다.",
                  hospital: "베터빌 병원",
                  date : "2022-09-30",
                  cnt : 5,
                }}
              />
              <CaseItem 
                value={{
                  img : dogImg,
                  title : "반려동물 시술에 대한 교육자료 공유",
                  text : "미국 항공우주국 홈페이지에 들어가면 누구나 허블 우주 망원경으로 관측해 촬영한 장엄한 우주 사진을 볼 수 있다. 장엄한 우주 사진을 볼 수 있다.",
                  hospital: "베터빌 병원",
                  date : "2022-10-10",
                  cnt : 10,
                }}
              />
            </div>
          </Contents>
          <Aside />
        </div>
      </Container>
    </>
  )
}

export default DignoseCase