import styled from "@emotion/styled";
import { ClampText } from "../../style/variable.style";
import logos from '../../images/txt_journal.png';



export const CommunityTb = styled.div`
  table{
    width: 100%;
  }
  th,td{
    padding:14px;
    font-size: 1.4rem;
    text-align: center;
    vertical-align:middle;
  }
  .align_left{
    text-align: left;
  }
  thead th{
    font-size: 1.6rem;
    font-weight: 600; 
    border-bottom:1px solid #000;
  }
  td{
    color: #666;
    border-bottom: 1px solid var(--form-border);
    &.title{
      color: #000;
      text-align: left;
      a:hover{
        text-decoration: underline;
      } 
    }
    .comm-cnt{
      display: inline-block;
      color: var(--primary-color);
      margin-left: 6px;
      font-weight: 600;
    }
  }
  
  &.news .title a{
    font-size: 1.6rem;
    display: inline-block;
    width: 740px;
    ${ClampText.df}
    box-sizing: border-box;
  }
`
export const InfoRow = styled.div`
   font-size:1.4rem;
  color:var(--c-gray);
  span + span:before{
    content:"";
    display:inline-block;
    width:1px;
    height:10px;
    margin-left:8px;
    margin-right:6px;
    background:var(--c-gray);
  }
`
export const ViewHeader = styled.div`
  padding:30px 0px;
  border-top:1px solid #000;
  border-bottom:1px solid var(--form-border);
  h2{
    margin-bottom:26px;
    font-size:2.8rem;
  }
`
export const ViewBody = styled.div`
  line-height:1.8;
  padding:30px 0px 60px;
  font-size:1.6rem;
  border-bottom:1px solid var(--form-border);
  p{
    margin-bottom:10px;
  }
  strong{
    display:block;
    margin-top:10px;
  }
  &.board strong{
    padding-top:10px;
    padding-bottom:10px;
  }
`
export const ViewFigrue = styled.div`
  figure, figcaption{
    max-width:560px;
    margin:0 auto;
  }
  figcaption{
    color:var(--c-gray);
  }
`
export const BtnGroups = styled.div`
  margin-top:20px;
  text-align:right;
  button{
    position:relative;
    padding:0 20px;
    line-height:30px;
    border:1px solid var(--form-border);
  }
  button+button{
    margin-left:6px;
  }
`
export const ReportLi  = styled.li`
  a{
    display:block;
    position:relative;
    border:1px solid var(--form-border);
    border-radius:30px;
    overflow:hidden;
    box-shadow:rgba(0, 0, 0, 0.1) 10px 10px 10px
  }
  .info{
    padding:35px 30px;
    height:186px;
    box-sizing:border-box;
    h3{
      margin-bottom:12px;
      font-weight:600;
      font-size:2.2rem;
    }
    p{
      width:300px;
      font-size:1.6rem;
      color:var(--c-gray);
      word-break:keep-all;
    }
  }
`

export const LogoBox = styled.div`
  height:122px;
  display:flex;
  justify-content:center;
  align-items:center;
  background:#F9F9F9;
  box-sizing:border-box;
  
  .journal{
    display:block;
    height:65px;
    background-image:url(${logos});
    &.logo1{
      width:133px;
      background-position-y:-70px;
    }
    &.logo2{
      width:190px;
      background-position-x:-140px;
    }
    &.logo3{
      width:114px;
      background-position-y:-130px;
    }
    &.logo4{
      width:87px;
      background-position-y:-195px;
    }
    &.logo5{
      width:150px;
      background-position:-140px -195px;
    }
    &.logo6{
      width:234px;
      background-position:-140px -260px;
    }
    &.logo7{
      width:110px;
      background-position-y:-260px;
    }
    &.logo8{
      width:167px;
      background-position-y:-325px;
    }
  }
`

export const ListTop = styled.div`
  display:flex;
  justify-content:space-between;
  margin-bottom:6px;
  strong{
    font-size:1.6rem;
  }
  ul{
    display:flex;
  }
  li a{
    display:inline-block;
    padding:0px 6px;
    color:var(--c-gray);
    font-size:1.4rem;
    &.on{
      font-weight:600;
    }
  }
`
export const CaseBox = styled.div`
  padding:30px 22px 33px;
  border:1px solid var(--form-border);
  border-left:2px solid var(--primary-color);
  margin-bottom:20px;
`
export const CaseBoxCont = styled.div`
  width:660px;
  overflow:hidden;
  strong{
    width:100%;
    display:block;
    padding-bottom:14px;
    font-size:2rem;
    font-weight:600;
    ${ClampText.df}
  }
  p{
    font-size:1.4rem;
    ${ClampText.line2}
  }
  .info{
    margin-top:14px;
    padding-top:14px;
    border-top:1px solid var(--form-border);
    font-size:1.2rem;
    .cnt{
      font-weight:bold;
      color: var(--primary-color);
    }
  }

`
export const EventLI = styled.li`
   a{
    display:block;
    position:relative;
    border:1px solid var(--form-border);
    border-radius:30px;
    overflow:hidden;
    box-shadow:rgba(0, 0, 0, 0.1) 10px 10px 10px
  }
  .cover{
    position:absolute;
    width:100%;
    height:150px;
    display:flex;
    justify-content:center;
    align-items:center;
    font-size:1.8rem;
    font-weight:600;
    color:#fff;
    background:rgba(0,0,0,.6);
  }
  figure{
    width:100%;
    height:150px;
    overflow:hidden;
  }
  figcaption{
    height:140px;
    padding:23px 30px;
    box-sizing:border-box;
    strong{
      display:block;
      margin-bottom:20px;
      font-size:1.8rem;
      font-weight:700;
      word-break:keep-all;
    }
    span{
      font-size:1.4rem;
    }
  }
`