import { Search } from "./com_layout.style";

export default function SearchBox() {
  return (
    <Search>
      <form action="">
        <fieldset>
          <legend>검색</legend>
          <input
            type="search"
            id=""
            title="검색어를 입력하세요"
            placeholder="검색하세요."
          />
          <button className="btn-sch">
            <span className="blind">검색 아이콘</span>
          </button>
        </fieldset>
      </form>
    </Search>
  );
}
