import React from 'react'
import { SchInputStyle } from './form.style'

const SchInput = () => {
  return (
    <li style={{flex:"1"}}>
      <SchInputStyle className="sch_box">
        <input type="text" placeholder="검색어 입력하세요"></input>
        <button className="btn">검색</button>
      </SchInputStyle>
    </li>
  )
}

export default SchInput