import React from 'react'
import { Link } from 'react-router-dom'
import { AllMenuStyle } from './com_layout.style'
import { Container, Flex } from '../../style/contents.style';
export interface AllMenuProps{
  isActive ?: boolean;
}
const AllMenue = ({isActive} : AllMenuProps) => {
  return (
    <>
      <AllMenuStyle className={isActive ? "on" : ""}>
        <Container pdt="0">
          <Flex alOpt="flex-start">
            <div className="col">
              <h2>클래스룸</h2>
              <ul>
                <li>
                  <Link to="/class">임상강좌 VOD</Link>
                </li>
                <li>
                  <Link to="/class/live">Live 세미나</Link>
                </li>
              </ul>
            </div>
            <div className="col">
              <h2>라이브러리</h2>
              <ul>
                <li>
                  <Link to="/library">베터빌 뉴스</Link>
                </li>
                <li>
                  <Link to="/library/reportsch">논문 검색</Link>
                </li>
              </ul>
            </div>
            <div className="col">
              <h2>커뮤니티</h2>
              <ul>
                <li>
                  <Link to="/community">자유 게시판</Link>
                </li>
                <li>
                  <Link to="/community/dignos_case">진단케이스</Link>
                </li>
                <li>
                  <Link to="/community/event">이벤트</Link>
                </li>
              </ul>
            </div>
          </Flex>
        </Container>
      </AllMenuStyle>
    </>
  )
}

export default AllMenue