import React from 'react'
import { Flex, Grid } from '../../style/contents.style'
import { Link } from 'react-router-dom'
import { Cate, ListDesc, ListTit } from '../List/list.style';

interface ListTypeProps {
  item: {
    id: number;
    link: string;
    img: string;
    alt: string;
    classify: string;
    title: string;
    desc: string;
  };
  showType: string;
}

const ListRecommItem = ({item, showType }: ListTypeProps) => {
  return (
    <>
      <li key={item.id}>
        <Link to="/" className='hover-underline'>
          <Flex>
            <figure className='flex-sh0'>
              <img src={item.img} alt={item.alt} />
            </figure>
            <div>
              <Cate>{item.classify}</Cate>
              <ListTit className="title">{item.title} </ListTit>
              {showType === "list" && 
                <ListDesc>{item.desc}</ListDesc>
              }
            </div>
          </Flex>
        </Link>
      </li>
    </>
  )
}

export default ListRecommItem