import React, { useState } from 'react'
import ListRecommData from '../../data/ListRecommData'
import ListRecommItem from './ListRecommItem'
import { Grid } from '../../style/contents.style';
import { BtnMore } from '../Layout/layout.style';
import DataNone from '../List/DataNone';

export interface ListTypeProps{
  showType: string;
}
const ListRecomm = ({showType} : ListTypeProps) => {
  const recommLength = ListRecommData.length;
  const [visibleItems, setVisibleItems] = useState(4);
  const visibleData = ListRecommData.slice(0, visibleItems);
  const handleShowMore = () => {
    setVisibleItems((prev) => prev + 4); 
  };
  return (
    <>
      {showType === "album" && recommLength > 0 && (
        <Grid columns="2">
          {visibleData.map((item) => (
            <ListRecommItem 
              key={item.id}
              item={item} 
              showType="album" 
            />
          ))}
        </Grid>
      )}
       {showType === "list" && recommLength > 0 && (
        <Grid columns="1">
          {visibleData.map((item) => (
            <ListRecommItem 
              key={item.id}
              item={item} 
              showType="list" 
            />
          ))}
        </Grid>
      )}
      {visibleItems < recommLength && (
        <BtnMore onClick={handleShowMore}>더보기</BtnMore>
      )}
      
      {!(showType === "album" && recommLength > 0) &&
        !(showType === "list" && recommLength > 0) && (
          <DataNone />
        )}
       
   </>
  )
}

export default ListRecomm