import React from 'react'
import { Container, Contents } from '../../style/contents.style'
import Aside from '../../common/Aside/Aside'
import { ViewHeader, ViewBody, ViewFigrue, BtnGroups, InfoRow } from '../community/community.style'

const NewsView = () => {
  return (
    <>
      <Container id="content">
        <div className='contents-inner'>
          <Contents>
            <ViewHeader>
              <h2>길고양이 TNR, 마당개 중성화 문제 많다' 수의사회 보이콧 움직임</h2>
              <InfoRow>
                <span>김가영</span>
                <span>하이닥 건강의학기자</span>
              </InfoRow>
            </ViewHeader>
            <ViewBody>
              <p>사랑을 하면 예뻐지고, 건강해진다는 말이 있다. 사랑이 외모는 물론 정신 및 신체 건강에도 좋은 영향을 미친다는 의미다. 실제로 사랑은 건강에 도움이 된다. 사랑에 빠진 연인들의 감정은 크게 ‘갈망, 끌림, 애착’의 3단계를 거치는데, 각 단계마다 뇌에서 내뿜는 호르몬이 건강상의 이점을 제공한다.</p>
              <p>사랑의 3가지 단계를 관장하는 호르몬 중 첫 번째는 ‘도파민’이다. 처음 상대방을 만나 사랑을 갈망할 때 분비된다. 이후 서로에게 강하게 끌리는 열정적인 사랑을 하는 단계에서는 ‘페닐에틸아민’의 역할이 크다. 열정적인 사랑이 지나가고 콩깍지가 벗겨지면, 애착 관계를 형성하는 ‘옥시토신’이 남아 안정적인 사랑기에 접어든다.</p>
              <ViewFigrue>
                <figure>
                  <img src="https://src.hidoc.co.kr/image/lib/2024/3/12/1710209075815_0.png" alt="사랑의 호르몬이 질병을 치료할 수 있다 | 출처: 게티이미지뱅크" />
                </figure>
                <figcaption>사랑의 호르몬이 질병을 치료할 수 있다 | 출처: 게티이미지뱅크</figcaption>
              </ViewFigrue>
              <strong>사랑에 빠뜨리는 호르몬 ‘도파민’, 파킨슨병 치료의 열쇠</strong>
              <p>갈망, 성취, 쾌감 등의 감각과 관련된 신호를 전달하는 도파민은 사랑에 빠질 때 분비가 크게 증가한다. 사랑하는 사람과 함께하고 싶은 갈망이 생기기 때문이다. 이런 갈망은 서로 연인이 되면서 해소되는데, 이때 느껴지는 성취와 쾌감은 또다시 도파민을 배출한다. 그리고 도파민은 상대방과 더 가까워지고 싶다는 새로운 심리적 갈망을 불러일으킨다. 심리적 갈망은 외부에 의욕으로 나타나게 되는데, 사랑에 빠진 사람들이 적극적인 태도로 변하는 이유다.</p>
            </ViewBody>
            <BtnGroups>
              <button>목록</button>
              <button className='prev'>이전글</button>
              <button className='next'>다음글</button>
            </BtnGroups>
          </Contents>
          <Aside />
        </div>
      </Container>
    </>
  )
}

export default NewsView