import styled from "@emotion/styled"
import { IcoCollect, PosCenterY } from "../../style/variable.style";

interface schProps {
  schWidth?: string;
  mbbVal?: string;
  bdColor?: string;
}

export const SchGroup = styled.ul<schProps>`
  margin-bottom:  ${(props) => (props.mbbVal ? props.mbbVal : '20px')};
  padding-bottom: 10px;
  width: ${(props) => (props.schWidth ? props.schWidth : '100%')};
  height: 34px;
  border-bottom: 1px solid ${(props) => (props.bdColor ? props.bdColor : '#111')};
  display: flex;
  align-items: center;
  gap: 10px;
  input,select{
    border:0;
  }
  li {
    position: relative;
    & > * {
      position: relative;
      height: 34px;
      font-size:1.6rem;
      box-sizing: border-box;
    }
  }

  li + li {
    padding-left:4px;
    &:before {
      content: "";
      display: block;
      width: 1px;
      height: 10px;
      background: #111;
      ${PosCenterY};
      left: 0;
    }
  }
  
  select{
    width:100%;
  }
`;


export const DateInput = styled.div`
  display: flex;
  input[type="text"] {
    width: 114px;
    padding: 0px 10px;
    box-sizing: border-box;
    font-family: var(--base-font);
    font-size:1.6rem
  }
`
export const SelectStyle = styled.select`
  border: none;
  font-weight:600;
`
export const SchInputStyle = styled.div`
  margin-left: 10px;
  box-sizing: border-box;
  input[type="text"] {
    width: 90%;
    height: 100%;
    padding: 0 10px;
    font-size:1.6rem;
    box-sizing: border-box;
  }
  .btn {
    ${IcoCollect};
    ${PosCenterY};
    right: 4px;
    width: 20px;
    height: 100%;
    background-position: -67px 8px;
    text-indent: -9999em;
  }
`